body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    color: rgba(255, 255, 255);
    text-shadow: 3px 3px 2px rgb(0, 0, 0);
    background: #222;
  }
  
  h1 {
    text-align: center;
  }
  
  
  
  .bpm-slider {
    text-align: center;
  }
  
  .bpm-slider fieldset {
    border: 2px solid white;
    -moz-border-radius: 8px;
    -webkit-border-radius: 8px;
    border-radius: 8px;
    max-width: 55%;
    margin: auto;
  
  }
  
  .bpm-slider input[type="range"] {
    width: 50%;
    margin: 25px;
  }
  
  input[type="range"] {
    -webkit-appearance: none;
    width: 100%;
    margin: 9.25px 0;
  }
  input[type="range"]:focus {
    outline: none;
  }
  input[type="range"]::-webkit-slider-runnable-track {
    width: 100%;
    height: 5.5px;
    cursor: pointer;
    box-shadow: 0px 0px 1px #000000, 0px 0px 0px #0d0d0d;
    background: #ffffff;
    border-radius: 4.4px;
    border: 0.3px solid #000101;
  }
  input[type="range"]::-webkit-slider-thumb {
    box-shadow: 0px 0px 1.4px #670000, 0px 0px 0px #810000;
    border: 2.7px solid #ffffff;
    height: 24px;
    width: 24px;
    border-radius: 25px;
    background: #ca3435;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -9.55px;
  }
  input[type="range"]:focus::-webkit-slider-runnable-track {
    background: #ffffff;
  }
  input[type="range"]::-moz-range-track {
    width: 100%;
    height: 5.5px;
    cursor: pointer;
    box-shadow: 0px 0px 1px #000000, 0px 0px 0px #0d0d0d;
    background: #ffffff;
    border-radius: 4.4px;
    border: 0.3px solid #000101;
  }
  input[type="range"]::-moz-range-thumb {
    box-shadow: 0px 0px 1.4px #670000, 0px 0px 0px #810000;
    border: 2.7px solid #ffffff;
    height: 24px;
    width: 24px;
    border-radius: 25px;
    background: #ca3435;
    cursor: pointer;
  }
  input[type="range"]::-ms-track {
    width: 100%;
    height: 5.5px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
  }
  input[type="range"]::-ms-fill-lower {
    background: #ffffff;
    border: 0.3px solid #000101;
    border-radius: 8.8px;
    box-shadow: 0px 0px 1px #000000, 0px 0px 0px #0d0d0d;
  }
  input[type="range"]::-ms-fill-upper {
    background: #ffffff;
    border: 0.3px solid #000101;
    border-radius: 8.8px;
    box-shadow: 0px 0px 1px #000000, 0px 0px 0px #0d0d0d;
  }
  input[type="range"]::-ms-thumb {
    box-shadow: 0px 0px 1.4px #670000, 0px 0px 0px #810000;
    border: 2.7px solid #ffffff;
    width: 24px;
    border-radius: 25px;
    background: #ca3435;
    cursor: pointer;
    height: 5.5px;
  }
  input[type="range"]:focus::-ms-fill-lower {
    background: #ffffff;
  }
  input[type="range"]:focus::-ms-fill-upper {
    background: #ffffff;
  }
  
  .metronome button {
    text-align: center;
    background: #ca3435;
    padding: 10px;
    margin-bottom: 3%;
    border: 1px solid #832420;
    -moz-border-radius: 8px;
    -webkit-border-radius: 8px;
    border-radius: 8px;
    width: 100px;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    
  }
  
  
  
  